/* --- dot container --- */
.page-scroll-dots {
    position: fixed;
    right: 30px;
    top: 50%;
    transform: translateY(-50%);
    display: flex;
    flex-direction: column;
    gap: 14px;
    z-index: 999;
}

/* --- circle dot --- */
.page-dot {
    width: 14px;
    height: 14px;
    border-radius: 50%;
    background-color: #d0c7b0;
    cursor: pointer;
    transition: all 0.3s ease;
    position: relative; /* tooltip ìœ„ì¹˜ ê¸°ë°˜ */
}

.page-dot.active {
    background-color: #60493a;
    transform: scale(1.3);
}

/* --- tooltip ê¸°ë³¸ ìˆ¨ê¹€ --- */
.dot-tooltip {
    position: absolute;
    right: 24px; /* ë™ê·¸ë¼ë¯¸ ì™¼ìª½ìœ¼ë¡œ í‘œì‹œ */
    top: 50%;
    transform: translateY(-50%);
    background: rgba(0, 0, 0, 0.85);
    color: #fff;
    padding: 4px 10px;
    font-size: 12px;
    border-radius: 6px;
    white-space: nowrap;
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.25s ease, transform 0.25s ease;
}

/* --- hover ì‹œ tooltip ë³´ì´ê¸° --- */
