    border-radius: 12px;
    height: 20px;
    /* 16px â†’ 20px */
    margin-left: 8px;
    position: relative;
}

.skill-fill {
    width: calc(100% - 8px);
    height: 100%;
    background-color: #ffe9b3;
    border-radius: 12px;
    text-align: right;
    padding-right: 6px;
    box-sizing: border-box;
    font-size: 12px;
    /* 11px â†’ 12px */
    line-height: 20px;
}

/* ë°˜ì‘í˜• */
@media (max-width: 1024px) {
    .profile-container {
        flex-direction: column;
        height: auto;
        padding-top: 60px;
        overflow-y: auto;
    }

    body {
        overflow: auto;
    }

    .profile-right {
        flex-direction: column;
        padding-left: 0;
        gap: 25px;
        width: 100%;
    }

    .profile-left {
        width: 100%;
        margin-bottom: 30px;
        /* ì™¼ìª½ í”„ë¡œí•„ê³¼ ì˜¤ë¥¸ìª½ ì •ë³´ ì‚¬ì´ ì—¬ìœ  */
    }

    .skillsec {
