        /* SKILLS ì„¹ì…˜ ì „ì²´ ë°•ìŠ¤ ì¡°ê¸ˆ ë” ìœ„ë¡œ */
    }

    .skill-bar {
        margin: 12px 0;
    }

    .skill-icon {
        width: 20px;
        height: 20px;
        margin-right: 10px;
    }

    .skill-bar span {
        width: 100px;
        font-size: 14px;
    }

    .skill-progress {
        height: 18px;
    }

    .skill-fill {
        font-size: 11px;
        line-height: 18px;
    }

    /* Contact ì•„ì´í…œ ë°˜ì‘í˜• */
    .profile-contact .info-item {
        grid-template-columns: 20px 80px auto;
        column-gap: 6px;
    }

    .profile-contact .info-label {
        font-size: 14px;
    }

    .profile-contact .info-text {
        font-size: 14px;
    }
}

@media (max-width: 600px) {
    .profile-image {
        width: 240px;
        height: 180px;
    }

    .profile-name {
        font-size: 20px;
    }

    .profile-birth {
        font-size: 16px;
    }

    .skillsec {
        margin-top: 20px;
    }

    .skill-bar {
        margin: 10px 0;
    }

    .skill-icon {
        width: 18px;
        height: 18px;
        margin-right: 8px;
    }

    .skill-bar span {
        width: 80px;
        font-size: 13px;
    }

    .skill-progress {
        height: 16px;
    }

    .skill-fill {
        font-size: 10px;
        line-height: 16px;
    }

    /* Contact ì•„ì´í…œ ëª¨ë°”ì¼ */
    .profile-contact .info-item {
        grid-template-columns: 18px 70px auto;
